import { Stack } from "@mui/system";
import { ItemCard } from "modules/main/blocks/itemCard";
import { MainLayout } from "other/layouts/mainLayout";
import { useTranslation } from "react-i18next";

import donate from "../../other/images/donate.png";
import makepayment from "../../other/images/makepayment.png";
import qr from "../../other/images/qr.png";

const Main = () => {
  const { t } = useTranslation();

  return (
    <Stack>
      <MainLayout>
        <Stack
          direction={{ lg: "row", md: "column", sm: "column", xs: "column" }}
          justifyContent="space-between"
          spacing={5}
        >
          <ItemCard
            title={t("main.getPayed")}
            image={makepayment}
            buttonColor="success"
            route="/main/qr"
            disabled={false}
          />
          <ItemCard
            title={t("main.scanQR")}
            image={qr}
            buttonColor="secondary"
            route="/main/readeqr"
            disabled={false}
          />
          <ItemCard
            title={t("main.makePayment")}
            image={donate}
            buttonColor="primary"
            route="/main/qr"
            disabled={true}
          />
        </Stack>
      </MainLayout>
    </Stack>
  );
};
export default Main;
