import { Box, Button, Typography } from "@mui/material";
import { Stack } from "@mui/system";
import { useNavigate } from "react-router";

export const ItemCard = ({
  title,
  image,
  buttonColor,
  route,
  disabled,
}: {
  title: string;
  image: string;
  buttonColor: string;
  route: string;
  disabled: boolean;
}) => {
  const navigate = useNavigate();
  return (
    <>
      <Button
        onClick={() => navigate(route)}
        color={buttonColor as "primary" | "secondary" | "success"}
        disabled={disabled}
      >
        <Stack
          gap={3}
          width={300}
          alignItems="center"
          py={{ lg: 5, md: 5, sm: 3, xs: 3 }}
        >
          <Box
            component="img"
            width={{ lg: 128, md: 128, sm: 100, xs: 100 }}
            src={image}
          />
          <Box>
            <Typography fontWeight={600} fontSize={18}>
              {title}
            </Typography>
          </Box>
        </Stack>
      </Button>
    </>
  );
};
